.navbar {
  width: 20vw;
  height: 100vh;
  display: flex;
  position: fixed;
  flex-direction: column;
  justify-content: center;
  background: white;
  box-shadow: 1px 0 10px rgba(0, 0, 0, 0.09); }

.navbar .profile {
  flex: 1; }

.navbar .profile h1 {
  color: #fff;
  text-transform: uppercase;
  letter-spacing: .25rem;
  font-size: 2.5rem;
  padding-top: 1rem;
  text-align: center; }

.navbar ul {
  width: 100%;
  list-style-type: none; }

.navbar ul li {
  padding-right: 1rem; }

.navbar ul li .active {
  transition: color .5s .25s;
  color: #016FB9; }

.navbar ul li .active:after {
  background: #016FB9;
  left: 5%;
  right: 5%; }

.navbar ul li a {
  text-decoration: none;
  color: #182825;
  font-weight: 700;
  display: inline-block;
  padding: 1rem;
  width: 100%;
  text-transform: uppercase;
  letter-spacing: 1px;
  background-position: 0;
  position: relative; }

.navbar ul li a:after {
  content: '';
  transition: all .5s;
  position: absolute;
  left: 50%;
  right: 50%;
  bottom: 10%;
  height: 3px; }

.navbar ul li a:not(.active):hover:after {
  background: #979797;
  left: 5%;
  right: 5%; }

.navbar ul li .icon {
  display: inline-block;
  width: 1rem;
  margin-right: .5rem; }
