* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

html {
  scroll-behavior: smooth; }

body {
  font-family: "Roboto Mono", -apple-system, BlinkMacSystemFont, Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background: #f7f7f7; }

h3 {
  font-size: 2rem;
  color: rgba(24, 40, 37, 0.8); }

h4 {
  font-size: 1.3rem;
  color: rgba(24, 40, 37, 0.8); }

section {
  display: grid;
  height: 100vh;
  max-width: 1100px; }

section > div {
  padding: 2rem; }

.content {
  padding-left: calc(20vw + 2rem); }

.navBarCon {
  width: 20vw;
  position: relative; }
