.profileContainer {
  display: flex;
  color: #182825;
  align-items: center; }

.profileContainer .particles {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0; }

.profileContainer .profile {
  padding: 2rem; }

.profileContainer .profile div {
  display: inline-block;
  min-width: 10rem;
  text-align: center;
  margin-right: .5rem;
  background: #016FB9;
  color: white; }

.profileContainer .profile div span:nth-child(2) {
  margin-bottom: 1rem; }

.profileContainer .profile > *:not(:last-child) {
  margin-bottom: 1rem; }

.profileContainer .profile h2 {
  font-size: 3rem; }

.profileContainer .profile .subHeading {
  font-size: 1.1rem;
  text-transform: uppercase;
  letter-spacing: 5px;
  color: #979797; }
